import React from 'react';

const Display = ({products,add}) => {
    
    
    return ( 

        
        <div className="productContainer">
            <table className="table">
                <tbody>
                {products&& products.map(ele=>{
                return(
                    <tr key={ele.product}>
                        <td>
                            {ele.product}
                        </td>
                        <td>
                            $ {ele.price}
                        </td>
                        <td>
                            <button className="btn btn-success" onClick={()=>add(ele)}>Add Item</button>
                        </td>
                    </tr>
                    
                    
                )
            })}
                </tbody>
            </table>
            
            
        </div>
     );
}
 
export default Display;