import React, { useEffect, useState } from 'react';

const Cart = (props) => {
    const{cart,handleDelete,totalCalc,emptyCart}=props

    const[direccion,setDireccion]=useState("")
    const [new_cart,setNewCart]=useState(cart)


    useEffect(()=>{
        setNewCart(cart)
    },[cart])

    
    function handlePedido(e){
        e.preventDefault()

        props.history.replace("/RoloDelihome")

        emptyCart()
        

       
    }
    
   
    
    
    
    return (  
        <div style={{display:"flex",flexDirection:"row",justifyContent:"space-around"}}>
            <div style={{overflow:"auto",height:"300px",width:"300px"}}>
                <table className="table">
                    <tbody>
                    {new_cart&&new_cart.map(p=>{
                        return(
                        <tr key={Math.random()}>
                            <td>{p.count}</td>
                            <td>{p.product}</td>
                            <td>{p.price} $</td>
                            <td><button className="btn btn-danger" onClick={()=>handleDelete(p)}>Delete</button></td>
                        </tr>)
                    })}
                    
                    </tbody>
                </table>
            </div>

            <div style={{display:"flex",flexDirection:"column",justifyContent:"space-around",borderLeft:"2px solid  rgb(224, 238, 224)",padding:"10px"}} >

                <span style={{fontSize:"40px"}} className="text text-success">Total: {totalCalc(cart)}$</span>

                <form onSubmit={(e)=>handlePedido(e)}>
                    <div className="form-group">
                        <label htmlFor="DIRECCION">Direccion de Envio</label>
                        <input type="text" className="form-control" id="DIRECCION" value={direccion} onChange={(e)=>setDireccion(e.currentTarget.value)}/>
                        
                    </div>
                
                    <button type="submit" className="btn btn-success">Mandar Pedido</button>
                </form>
            
            </div>
        </div>
        
    );
}
 
export default Cart;