import React, { useEffect, useState } from 'react';
import {BrowserRouter, NavLink, Redirect, Route, Switch } from 'react-router-dom';
import {productList} from "./components/productsJason"
import Display from "./components/display"
import Cart from './components/displayCart';
import Nav from './components/nav';
import SideBar from './components/sideBar';
import Home from "./components/home"



const App = () => {

    const [products,setProduts]=useState(null)
    const [display,setDisplay]=useState(null)
    const [cart,setCart]=useState([])
   
   
    
    
    

    
    
    
    

    useEffect(()=>{

        setProduts(productList)
        
    },[])
    

    function handleProduct(categorie){
        const list =products.filter(c=>categorie.categorie===c.categorie)
        
        setDisplay(list[0].list)
        
    }

    function addToCart(p){

        let alReadyinCart=false

        cart.forEach(pro=>{
            if(p.product===pro.product){
                
                const new_cart=cart
                const index=cart.indexOf(pro)
                new_cart[index].count++
                
                setCart([...new_cart])
                alReadyinCart=true
                
            }
            
            
        })

        if(alReadyinCart===false)setCart([...cart,{...p,count:1}])



    }

    function handleDelete(p){

        if(p.count>1){
            const new_cart=cart
            const index=cart.indexOf(p)
            new_cart[index].count--
            
            setCart([...new_cart])
        }
        else {
            const index=cart.indexOf(p)
            const new_cart=cart
            new_cart.splice(index,1)
        
            setCart([...new_cart])
        }
        
        
    }

    function totalCalc(money){
        let suma=[]
        cart.forEach(x=>suma.push(x.price*x.count))
        let total=suma.reduce((a,b)=>a+b,0)
        return total
    }

    function emptyCart(){
        setCart([])
    }

    function itemsCount(list){
        let num=0

        list.forEach(ele=>num=ele.count+num)

        
        return num
    }

    
    

    

    
    
    return ( 
     <React.Fragment>
         <BrowserRouter>
         
         <main id="container">

            
            
            <Nav cart={cart} cartsItems={itemsCount(cart)}/>

            <SideBar products={products} handleProduct={handleProduct}/>

            <div id="box3" >
                <Switch>
                    <Route path="/RoloDelihome" exact component={Home}/>
                    <Route path="/bebidas" exact render={()=><Display products={display} add={addToCart}/>}/>
                    <Route path="/ensaladas" exact render={()=><Display products={display} add={addToCart}/>}/>
                    <Route path="/carnes blancas" exact render={()=><Display products={display} add={addToCart}/>}/>
                    <Route path="/carnes rojas" exact render={()=><Display products={display} add={addToCart}/>}/>
                    <Route path="/postres" exact render={()=><Display products={display} add={addToCart}/>}/>
                    <Route path="/cart" exact render={(props)=><Cart cart={cart} {...props}  emptyCart={emptyCart} handleDelete={handleDelete} totalCalc={totalCalc}/>}/>
                    <Route path="notfound" render={()=><h1>Not Found</h1>}/>
                    <Redirect to="/home"></Redirect>

                </Switch>
                
            </div>
            
        </main>

         </BrowserRouter>
        
     </React.Fragment>

     );
}
 
export default App;