import React, { useEffect, useState } from 'react';
import {NavLink} from "react-router-dom"


const Nav = ({cart,cartsItems}) => {

    
    


    return ( 
        <div id="box1">
                <div style={{marginLeft:"20px"}}>
                    <h2><NavLink to="/RoloDelihome" style={{color:"rgb(9, 102, 100)"}}>Rolo Deli</NavLink></h2>
                    

                </div>

                <NavLink to="/cart">
                    <div id="shoppingCart">
                        {cartsItems> 0 &&<div id="cartFull">{cartsItems}</div>}
                    </div>
                </NavLink>

            </div>
     );
}
 
export default Nav;