import React from 'react';
import {NavLink} from "react-router-dom"

const SideBar = ({products,handleProduct}) => {
    return ( 

        <div id="box2" >
                
    {products&&products.map(ele=><NavLink onClick={()=>handleProduct(ele)}  style={{color:"rgb(9, 102, 100)"}} to={`/${ele.categorie}`} key ={ele.categorie}>{ele.categorie.toUpperCase()}</NavLink>)}
    


            </div>
     );
}
 
export default SideBar;